<template>
  <div name="affiliations">
    <CoverImage page="Affiliations" backgroundImage="backgnd_3.jpg" title="Our Affiliations" />

    <v-container>
      <br />
      <br />
      <v-layout row wrap class="ma-2">
        <!-- Affiliations Page -->
        <v-flex>
          <v-img
            :src="require('@/assets/images/affiliations/affiliations.png')"
            alt="Our Affiliations"
          />
        </v-flex>
      </v-layout>
    </v-container>

    <br />
    <br />
  </div>
</template>

<script>
import CoverImage from "@/components/common/CoverImage.vue";

export default {
  name: "Affiliations",
  components: {
    CoverImage
  },
  created() {
    const plugin = document.createElement("script");
    plugin.setAttribute("src", "https://widget.getyourguide.com/v2/widget.js");
    plugin.async = true;
    plugin.defer = true;
    document.head.appendChild(plugin);
  }
};
</script>



